<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>参数分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <!-- 警告 -->
      <el-alert title="注意:只允许为第三级分类设置相关参数" type="warning" effect="dark"></el-alert>
      <el-row>
        <el-col>
          <span>请选择商品分类:</span>
          <!-- 级联选择器 -->
          <el-cascader
            v-model="selectCatKeys"
            :options="cateList"
            :props="{ expandTrigger: 'hover',label:'cat_name',value:'cat_id' }"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- tabs标签 -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <!-- 动态属性区域 -->
        <el-tab-pane label="动态属性" name="many">
          <el-button type="primary" :disabled="isDisabled" @click="addDialogVisible=true">添加参数</el-button>
          <!-- 表格 -->
          <el-table :data="manyTableData" border style="width: 100%">
            <el-table-column type="expand">
              <template slot-scope="scope">

                <el-tag
                  v-for="(item,i) in scope.row.attr_vals"
                  :key="i"
                  closable
                  @close="delTag(i,scope.row)"
                >{{item}}</el-tag>
                <!-- 添加新标签 -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="参数名称" prop="attr_name"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showEditDialog(scope.row.attr_id)"
                >编辑</el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="delParams(scope.row.attr_id)"
                >删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!-- 静态属性区域 -->
        <el-tab-pane label="静态属性" name="only">
          <el-button type="primary" :disabled="isDisabled" @click="addDialogVisible=true">添加属性</el-button>
          <!-- 表格区域 -->
          <el-table :data="onlyTableData" border style="width: 100%">
            <el-table-column type="expand">
                <template slot-scope="scope">

                </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="属性名称" prop="attr_name"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="showEditDialog(scope.row.attr_id)"
                >编辑</el-button>
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="delParams(scope.row.attr_id)"
                >删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!-- 添加动态参数的对话框 -->
    <el-dialog
      :title="'添加'+titleText"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addFromReset"
    >
      <!-- 主体区域 -->
      <el-form :model="addFormData" :rules="addFormDataRules" ref="addFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addFormData.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addParams">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑参数的对话框 -->
    <el-dialog
      :title="'编辑'+titleText"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="resetEditDiaLog"
    >
      <!-- 修改表单 -->
      <el-form :model="editFormData" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editFormData.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveEdit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 商品分类列表
      cateList: [],
      // 级联选择框双向绑定的数组
      selectCatKeys: [],
      // 动态属性数据
      manyTableData: [],
      //   静态属性数据
      onlyTableData: [],
      // 默认显示动态属性
      activeName: 'many',
      // 控制添加参数对话框
      addDialogVisible: false,
      //   添加表单数据
      addFormData: {
        attr_name: ''
      },
      //   添加表单数据规则
      addFormDataRules: {
        attr_name: [
          { required: true, message: '请输入参数名称', trigger: 'blur' }
        ]
      },
      //  控制编辑对话框显示隐藏
      editDialogVisible: false,
      // 修改对话框数据
      editFormData: {
        attr_name: ''
      },
      editFormRules: {
        attr_name: [
          { required: true, message: '请输入参数名称', trigger: 'blur' }
        ]
      },
      //   控制input显示与隐藏
      inputVisible: false,
      // input值
      inputValue: ''
    }
  },
  methods: {
    //   获取商品列表
    async getCateList () {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 3 }
      })
      if (res.meta.status != 200) return this.$message.error('获取失败')
      // 成功赋值给数组
      this.cateList = res.data
    },
    // 级联选择器改变触发
    handleChange () {
      // 判断选中id长度 必须要第三级
      //   if (this.selectCatKeys.length != 3) {
      //     this.selectCatKeys = [];
      //     return;
      //   }
      //   根据所选id和所在面板获取参数
      //   const { data: res } = await this.$http.get(
      //     `categories/${this.cateId}/attributes`,
      //     {
      //       params: {
      //         sel: this.activeName
      //       }
      //     }
      //   );
      //   if (res.meta.status != 200) this.$message.error("获取失败");
      //   // 赋值给数组
      //   console.log(res.data);
    //   if(this.selectCatKeys.length != 3) {
    //       this.selectCatKeys = []

      //       return
      //   }
      this.getParamsData()
    },
    // 切换tables
    handleTabClick () {
      console.log(this.activeName)
      this.getParamsData()
    },
    // 获取参数
    async getParamsData () {
      if (this.selectCatKeys.length != 3) {
        this.selectCatKeys = []
        this.manyTableData = []
        this.onlyTableData = []
        return
      }
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes`,
        {
          params: {
            sel: this.activeName
          }
        }
      )
      if (res.meta.status != 200) this.$message.error('获取失败')
      // 把attr_vals转换成数组
      res.data.forEach(item => {
        // 将数据分隔成数组 显示tag标签
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 控制文本框的显示隐藏
        item.inputVisible = false
        // 文本框输入额值
        item.inputValue = ''
      })
      console.log(res.data)
      // 成功后判断所在页面

      if (this.activeName == 'many') {
        this.manyTableData = res.data
        // console.log(this.manyTableData);
      } else {
        this.onlyTableData = res.data
        // console.log(this.onlyTableData);
      }
    },
    // 关闭对话框重置
    addFromReset () {
      this.$refs.addFormRef.resetFields()
    },
    // 实现添加功能
    addParams () {
      // 进行表单预验证
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return this.$message.error('验验证失败')
        // 成功发送请求添加参数
        const { data: res } = await this.$http.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.addFormData.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status != 201) return this.$message.error('添加失败')
        // 成功 重新获取参数
        this.getParamsData()
        // 关闭对话框
        this.addDialogVisible = false
      })
    },
    // 显示编辑对话框
    async showEditDialog (attr_id) {
      // 通过id查询参数
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes/${attr_id}`
      )
      if (res.meta.status != 200) return this.$message.error('查询失败')
      // 成功将data赋值给修改表单数据
      this.editFormData = res.data
      this.editDialogVisible = true
    },
    // 实现修改功能
    saveEdit () {
      // 表单预验证
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return this.$message.error('验证失败')
        // 成功发送修改请求
        const { data: res } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.editFormData.attr_id}`,
          {
            attr_name: this.editFormData.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status != 200) return this.$message.error('更新失败')
        // 成功重新获取参数列表
        this.getParamsData()
        // 关闭对话框
        this.editDialogVisible = false
      })
    },
    // 关闭对话框重置
    resetEditDiaLog () {
      this.$refs.editFormRef.resetFields()
    },
    // 实现删除参数
    delParams (attr_id) {
      console.log(attr_id)
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          //  发送删除请求
          const { data: res } = await this.$http.delete(
            `categories/${this.cateId}/attributes/${attr_id}`
          )
          if (res.meta.status != 200) return this.$message.error('删除失败')
          // 删除成功重新获取数据
          this.getParamsData()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 实现删除标签
    delTag (i, row) {
      row.attr_vals.splice(i, 1)
      this.saveAttrValues(row)
    },
    async saveAttrValues (row) {
      // 需要发起请求，保存这次操作
      const { data: res } = await this.$http.put(
        `categories/${this.cateId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(' ')
        }
      )
      if (res.meta.status != 200) {
        return this.$message.error('修改参数失败')
      }
      this.$message.success('修改参数成功')
    },
    // 文本框按下了enter按钮或者失去了焦点
    async handleInputConfirm (row) {
      console.log('ok')
      // 没输入内容
      if (row.inputValue.trim().length == 0) {
        row.inputValue == ''
        row.inputVisible = false
        return
      }
      // 说明输入了内容
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      row.inputVisible = false
      // 发送请求保存此次操作
      const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join('')
      })
      console.log(res)
      if (res.meta.status != 200) return this.$message.error('修改失败')
    },
    // 显示input
    showInput (row) {
      row.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    }
  },
  created () {
    // 获取商品列表
    this.getCateList()
    // 获取动态属性
  },
  computed: {
    //  按钮是佛禁用
    isDisabled () {
      if (this.selectCatKeys.length != 3) {
        return true
      } else {
        return false
      }
    },
    cateId () {
      if (this.selectCatKeys.length == 3) {
        return this.selectCatKeys[2]
      } else {
        return null
      }
    },
    // 文章标题
    titleText () {
      if (this.activeName == 'many') {
        return '动态参数'
      } else {
        return '静态属性'
      }
    }
  }
}
</script>
<style scoped lang="less">
.el-alert__title {
  font-size: 16px !important;
}
.el-row {
  margin-top: 10px;
  font-size: 16px !important;
}
.el-cascader {
  margin-left: 8px;
}
.el-tabs {
  margin-top: 10px;
}
.el-tag {
  margin-right: 8px;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
}
</style>
