<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <el-button type="primary" @click="showAddForm">添加分类</el-button>
      <!-- 表格树 -->
      <table-tree
        :data="cateList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
      >
        <!-- 是否有效 slot与 slot-scope等效-->
        <template slot="isUseful" scope="scope">
          <i class="el-icon-success" v-if="scope.row.cat_deleted==false" style="color:green"></i>
          <i class="el-icon-error" v-else style="color:error"></i>
        </template>
        <!-- 排序 -->
        <template slot="sort" slot-scope="scope">
          <div>
            <el-tag type="danger" v-if="scope.row.cat_level==0">一级</el-tag>
            <el-tag type="success" v-else-if="scope.row.cat_level==1">二级</el-tag>
            <el-tag type="success" v-else>三级</el-tag>
          </div>
        </template>
        <!-- 操作 -->
        <template slot="caozuo" slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini">编辑</el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini">删除</el-button>
        </template>
      </table-tree>
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!-- 添加分类对话框 -->
    <el-dialog title="提示" :visible.sync="addCatrgoryDialogVisible" width="50%" @close ="resetAddCate">
      <!-- 提交表单 -->
      <el-form
        :model="addCategoryFormData"
        :rules="addCategoryFormRules"
        ref="addCategoryFormRef"
        label-width="100px"
      >
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="addCategoryFormData.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类">
          <!-- 级联选择器 -->
          <!-- options用来指定数据源
              props用来指定配置对象
           -->
          <el-cascader
            v-model="selectedKeys"
            :options="parentCateList"
            :props="{expandTrigger:'trigger',label:'cat_name',value:'cat_id',checkStrictly:'true'}"
            @change="parentCateChange"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <!-- 底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCatrgoryDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCategory">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 查询参数
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 商品分类数据列表
      cateList: [],
      // 总数据条数
      total: null,
      columns: [
        {
          label: '分类名称',
          prop: 'cat_name'
        },
        {
          label: '是否有效',
          prop: 'cat_deleted',
          type: 'template', // 表示自定义模板
          template: 'isUseful' // 具名插槽
        },
        {
          label: '排序',
          prop: 'cat_deleted',
          type: 'template',
          template: 'sort'
        },
        {
          label: '操作',
          prop: 'cat_deleted',
          type: 'template',
          template: 'caozuo'
        }
      ],
      //   控制添加分类对话框
      addCatrgoryDialogVisible: false,
      // 添加分类数据
      addCategoryFormData: {
        // 将要添加分类的名称
        cat_name: '',
        // 父级分类的id
        cat_pid: 0,
        // 分类等级  默认添加的是一级分类
        cat_level: 0
      },
      // 添加分类数据规则
      addCategoryFormRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ]
      },
      // 父级分类列表
      parentCateList: [],
      // 指定级联器的配置对象
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      selectedKeys: []// 选中的父级分类

    }
  },
  methods: {
    // 获取商品分类数据
    async getCateList () {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      console.log(res)
      if (res.meta.status != 200) { return this.$message.error('获取商品分类失败') }
      // 请求成功
      this.cateList = res.data.result
      this.total = res.data.total
    },
    // 处理每页数量
    handleSizeChange (newsize) {
      // 将新的每页大小赋值
      this.queryInfo.pagesize = newsize
      // 重新请求数据
      this.getCateList()
    },
    // 处理当前为哪页
    handleCurrentChange (newpage) {
      // 将新的页数辅助
      this.queryInfo.pagenum = newpage
      // 重新请求数据
      this.getCateList()
    },
    // 显示添加分类对话框
    showAddForm () {
      // 调用函数 获取父级分类数据
      this.getParentCateList()
      this.addCatrgoryDialogVisible = true
    },
    // 获取父级分类数据
    async getParentCateList () {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      if (res.meta.status != 200) this.$message.error('获取父级分类数据失败')
      // 成功
      this.parentCateList = res.data
      console.log(this.parentCateList)
    },
    // 选中项发生变化触发这个函数
    parentCateChange () {
      console.log(this.selectedKeys)
      // 如果这个数组长度大于0 说明选中了父级分类
      if (this.selectedKeys.length > 0) {
        this.addCategoryFormData.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
        //  等级就为数组的长度  长度为2level值为2等级就为3
        this.addCategoryFormData.cat_level = this.selectedKeys.length
      }
    },
    // 实现添加分类
    async addCategory () {
      const { data: res } = await this.$http.post('categories', this.addCategoryFormData)
      console.log(res)
      if (res.meta.status != 201) this.$message.error('创建失败')
      // 成功 重新获取分类列表
      this.getCateList()
      // 关闭对话框
      this.addCatrgoryDialogVisible = false
    },
    // 实现关闭重置添加对话框
    resetAddCate () {
      this.$refs.addCategoryFormRef.resetFields()
      // 是级联选择器清空
      this.selectedKeys = []
      // 重置id和level防止上次选中关闭的id和level影响下一次添加
      this.addCategoryFormData.cat_id = 0
      this.addCategoryFormData.cat_level = 0
    }
  },
  created () {
    this.getCateList()
  }
}
</script>
<style scoped lang="less">

</style>
<style  lang="less">
.el-cascader-menu {
  height: 240px;
}
</style>
